.main{
    display:flex;
    justify-content:center;
    align-items:center;
  }
  
  .box{
    max-width: 500px;
    display: flex;
    flex-direction:column;
    align-items: center;
    margin:10px;
    img{
      width: 80%;
      transition:.4s;
    }

  }
  button{
    background: #f7f8aa;
    padding: 10px 20px;
    border-radius: 3px;
    margin:4px;
    border: 1px solid red;
    cursor: pointer;
    &:hover{
      background: bisque;
      border: 1px solid #000;
  
    }
  }

.tie {
  border:5px solid darkgray;
  color:darkgray;
  background:#fff;
}
.lose {
  border:5px solid lightgray;
  color:lightgray;
  background:white;
}
.win {
  border:5px solid red;
  background:fff;
  color:red
}
.resultP {
  font-size: 50px;
  font-weight:bold;
  text-transform: uppercase;
  color:#fff;
  background:red;
  border-top:5px solid red;
  border-bottom:5px solid red;
  padding:20px 30px;
}